import "./styles.css";
var colour = document.getElementById("input_box");
var bg_color = document.getElementById("bg_col");
var text_color = document.getElementById("text");
bg_color.style.backgroundColor = "white";
colour.addEventListener("click", function () {
  if (colour.checked) {
    bg_color.style.backgroundColor = "black";
    text_color.style.color = "white";
  } else {
    bg_color.style.backgroundColor = "white";
    text_color.style.color = "black";
  }
});
